export const apiPostAdvertisement = (
    type: number,
    relateType: number,
    relateId: string,
    url: string,
    title: string,
    content: string,
    image: string,
    cityCode: string
) => {
    return axios({
        method: "post",
        url: `/advertisement/add`,
        data: {
            "type": type,
            "relateType": relateType,
            "relateId": relateId,
            "url": url,
            "title": title,
            "content": content,
            "image": image,
            "cityCode": cityCode
        }
    })
}

