export const apiAddRecommend = (
    type: number, relateType: number, relateId: string,
    name: string, startTime: string, endTime: string, cityCode: string, userId: string, deviceType?: number,
    deviceName?: string, deviceId?: string) => {
    return axios({
        method: "post",
        url: `/recommend/add`,
        data: {
            "type": type,
            "relateType": relateType,
            "relateId": relateId,
            "name": name,
            "startTime": startTime,
            "endTime": endTime,
            "cityCode": cityCode,
            "deviceType": 3,
            "deviceName": "pc",
            "deviceId": "pc",
            "userId": userId,
        }
    })
}

export const apiDeleteRecommend = (ids:string[])=>{
    return axios({
        method:"post",
        url:`/recommend/delete`,
        data:{
            "ids":ids
        }
    })
}

